.events {
  position: relative; }
  .events__button button {
    z-index: 100;
    line-height: 25px;
    border: 2px solid white;
    border-radius: 3px;
    background-color: transparent;
    color: #2d3b4d;
    text-shadow: 1px 1px 3px white;
    cursor: pointer;
    outline: none;
    margin: 0 5px; }
    .events__button button:hover {
      background-color: #2d3b4d;
      color: white;
      text-shadow: 1px 1px 3px #2d3b4d; }
  .events__list {
    top: 0;
    position: absolute;
    left: 0;
    z-index: 200;
    width: 290px;
    background-color: #2d3b4d;
    border: 1px solid white;
    border-radius: 3px;
    color: white;
    cursor: pointer; }
    .events__list .event {
      padding: 5px;
      border: 1px solid white;
      border-radius: 3px; }
      .events__list .event p span {
        font-style: italic; }



