.add_artist p {
  margin: 5px 0; }
  .add_artist p input {
    padding: 5px;
    margin: 5px;
    min-width: 280px;
    width: 90%;
    outline: none;
    border: 2px solid #2d3b4d;
    font-size: 16px; }
  .add_artist p .invalid_val {
    border-color: coral; }
  .add_artist p button {
    line-height: 25px;
    border: 2px solid white;
    border-radius: 3px;
    background-color: transparent;
    color: #2d3b4d;
    text-shadow: 1px 1px 3px white;
    cursor: pointer;
    outline: none;
    margin: 0 15px; }
    .add_artist p button:hover {
      background-color: #2d3b4d;
      color: white;
      text-shadow: 1px 1px 3px #2d3b4d; }



